import { appendChild, appendToBefore } from "../../utils/rearrangeLayout";
import HandleLists from "../../utils/handleList";

export default () => {
  setTimeout(() => {
    appendChild("#content", "#home");
    appendToBefore("#footer .footer-links", ".body__item > p");

    const handleHomeCenterMenu = new HandleLists(
      ".body__item > ul.footer-links > li"
    );
    handleHomeCenterMenu.reArrangeMenu("li > a > img", "li > a");
  });
};
