class HandleEvents {
  constructor(strTarget) {
    this.t = document.querySelector(strTarget);
  }

  routerToPage = strLink => {
    this.t.addEventListener("click", () => {
      window.location.replace(strLink);
    })
  };

  addAttr = (attKey, attVal) => {
    this.t[`${attKey}`] = attVal;
  };

  setAttrChildList = (children, attrs) => {
    const c = this.t.querySelectorAll(children);
    attrs.map(ia => {
      const { key, val } = ia;
      c.forEach(item => {
        item[`${key}`] = val;
      });
    });
  };

  addClass = (targetEvent, strName) => {
    const te = document.querySelector(targetEvent);
    te.addEventListener("click", () => {
      this.t.classList.add(strName);
    });
  };

  removeChildAttr = (children, attrKey) => {
    const c = this.t.querySelectorAll(children);

    attrKey.map(itemAttrKey => {
      c.forEach(item => {
        item.removeAttribute(itemAttrKey);
      });
    });
  };

  removeAttr = attrKey => {
    attrKey.map(itemAttrKey => {
      this.t.removeAttribute(itemAttrKey);
    });
  };

  removeClass = (targetEvent, strName) => {
    const te = document.querySelector(targetEvent);
    te.addEventListener("click", () => {
      this.t.classList.remove(strName);
    });
  };
  // cross-browser: element.className.replace(/\bmystyle\b/g, "");

  convertHtmlStrToDom = htmlString => {
    const div = document.createElement("div");
    div.innerHTML = htmlString.trim();
    return div.firstChild;
  };

  replaceHtml = (targetEvent, hiddenHtml, displayHtml) => {
    const te = document.querySelector(targetEvent);
    if (te) {
      te.addEventListener("click", () => {
        hiddenHtml.map(item => {
          const itemTarget = document.querySelector(item);
          itemTarget.classList.add("hiddenElement");
        });
        displayHtml.map(item => {
          const itemTarget = document.querySelector(item);
          itemTarget.classList.remove("hiddenElement");
        });
      });
    }
  };

  handleAction = (targetEvent, actions) => {
    const te = document.querySelector(targetEvent);

    if (te) {
      te.addEventListener("click", () => {
        actions.map(action => {
          switch (true) {
            case action.name === "add":
              const tha = document.querySelector(action.html); // tha: targetHtmlAdd
              tha.classList.add(action.class);
              break;
            case action.name === "remove":
              const thr = document.querySelector(action.html); // thr: targetHtmlRemove
              thr.classList.remove(action.class);
              break;
            default:
              break;
          }
        });
      });
    } else {
      console.log("target HTML is not available!");
    }
  };
}

export default HandleEvents;
