class CarouselFlex {
    constructor(targetHtml, targetButton) {
        this.t = document.querySelector(targetHtml);
        this.b = document.querySelector(targetButton);
        this.move = 0;
    }

    init() {
        const il = this.t.querySelectorAll('li'); // itemLi
        Array.from(il).map((item, i) => {
            item.style.order = i;
        })
    }

    moveLeft() {
        const ch = this.t; // cu: carouselHtml
        const il = this.t.querySelectorAll('li'); // itemLi

        this.b.addEventListener('click', () => {
            if(screen.width >= 1024) {
                if(this.move > (il[0].clientWidth * il.length - il[0].clientWidth * 6)) {
                    this.move = 0;
                } else {
                    this.move += il[0].clientWidth;
                }
            } else {
                if(this.move > (il[0].clientWidth * il.length - il[0].clientWidth * 4)) {
                    this.move = 0;
                } else {
                    this.move += il[0].clientWidth;
                }
            }

            ch.style.left = `-${this.move}px`
        })

    }
}

export default CarouselFlex;