import { appendChild, addElement, addAttr, replaceText } from "../../utils/rearrangeLayout";
import selectBox from "../../utils/selectbox";
export default () => {
  setTimeout(() => {
    resetLayout();
    if (resetLayout()) {
      addLogo();
      addCheckmark();

      addElement(
        '<h2>PERSONAL INFORMATION</h2>',
        '#capture_traditionalRegistration_form_item_newPasswordConfirm'
      );

      addElement(
        '<h2>PROFESSIONAL INFORMATION</h2>',
        '#capture_traditionalRegistration_form_item_phone'
      );

      // Term of Use
      addAttr(
        '#capture_traditionalRegistration_form_item_acceptTermsOfUse #capture_traditionalRegistration_form_item_inner_acceptTermsOfUse label a',
        'title',
        'MSDâs Terms of Use'
      );
      addAttr(
        '#capture_traditionalRegistration_form_item_acceptTermsOfUse #capture_traditionalRegistration_form_item_inner_acceptTermsOfUse label a',
        'href',
        'https://www.msdconnect.sg/terms_of_use.xhtml'
      );
      replaceText(
        '#capture_traditionalRegistration_form_item_acceptTermsOfUse #capture_traditionalRegistration_form_item_inner_acceptTermsOfUse label a',
        'MSDâs Terms of Use'
      );
      // Privacy Policy
      addAttr(
        '#capture_traditionalRegistration_form_item_acceptTermsOfUse #capture_traditionalRegistration_form_item_inner_acceptTermsOfUse label a',
        'title',
        'MSDâs Privacy Policy'
      );
      addAttr(
        '#capture_traditionalRegistration_form_item_acceptTermsOfUse #capture_traditionalRegistration_form_item_inner_acceptTermsOfUse label a',
        'href',
        'https://www.msdconnect.sg/privacy_policy.xhtml'
      );
      replaceText(
        '#capture_traditionalRegistration_form_item_acceptPrivacyPolicy #capture_traditionalRegistration_form_item_inner_acceptPrivacyPolicy label a',
        'MSDâs Privacy Policy'
      );

      selectBox();
    } else {
      console.log('reset layoput was failed!');
    }
  });
  const resetLayout = () => {
    appendChild("#content", "#registration");
    return true;
  }
  const addLogo = () => {
    addElement(
      '<a class="img_wrap" href=""><img src="https://oncology-msdconnect-sg-dev.epublishmerck.com/static/mcisg/images/user.svg" alt="" /></a><h2>log in information</h2>',
      "#traditionalRegistration > .capture_header h1"
    );
  }
  const addCheckmark = () => {
    addElement(
      '<span class="checkmark"></span>',
      '#capture_traditionalRegistration_form_item_inner_brandedConsent label input'
    );
    addElement(
      '<span class="checkmark"></span>',
      '#capture_traditionalRegistration_form_item_acceptTermsOfUse label input'
    );
    addElement(
      '<span class="checkmark"></span>',
      '#capture_traditionalRegistration_form_item_acceptPrivacyPolicy label input'
    );
  }
}