import { appendChild, addElement, addAttr, replaceText } from "../../utils/rearrangeLayout";
export default () => {
  setTimeout(() => {
    resetLayout();
    if (resetLayout()) {
      addLogo();
    } else {
      console.log('reset layout was failed!');
    }
  });
  const resetLayout = () => {
    appendChild("#content", "#changePasswordTmpl");
    return true;
  }
  const addLogo = () => {
    addElement(
      '<a class="img_wrap" href=""><img src="https://oncology-msdconnect-sg-dev.epublishmerck.com/static/mcisg/images/lock_icon.svg" alt="" /></a><h2>log in information</h2>',
      "#changePassword > .capture_header h1"
    );
  }
}