import { isProd, pageUrl } from "../env";

function isAuth() {
    var ls = localStorage.getItem('janrainCaptureProfileData');
    if (ls) {
        var profileObj = JSON.parse(ls);
        return profileObj.uuid ? true : false;
    } else {
        return false;
    }
}

export const redirectAuth = () => {
    const textUrl = window.location.host + window.location.pathname;

    if (textUrl === pageUrl.login) {
        if (isAuth()) {
            // window.history.back(); // <-- it does not work on tridion
            // window.history.go(-1); // <-- it's worked on tridion
            !isProd ? 
            window.location.replace('/index.html') :
            window.location.replace('/')
        }
    }
    else {
        if(
            textUrl !== pageUrl.registration &&
            textUrl !== pageUrl.forgotpassword
        ) {     
            if (!isAuth()) {
                // PREVIEW Build
                // window.location.replace('localhost:8080/login.html');
                // PROD Build
                isProd ?
                window.location.replace('/login.xhtml'):
                window.location.replace('/login.html');
            }
        }
    }
}

export const signout = () => {
    setTimeout(function () {
        var signout = document.querySelector('.signout.btn');
        if (signout) {
            signout.addEventListener("click", function () {
                localStorage.clear();
                // PREVIEW Build
                // window.location.replace('/login.html');
                 // PROD Build
                isProd ?
                window.location.replace('/login.xhtml') :
                window.location.replace('/login.html')
            });
        }
    })
}


