import './less/style.less';
import { appendToBefore } from "./utils/rearrangeLayout";

// INJECTTING All JS OF EACH PAGE | PARTIAL HERE:
import MenuRight from "./components/partials/MenuRight";
import Home from "./components/page-home";
import Lung from "./components/page-lung";
import L1CT from "./components/page-lung-1l-combination-therapy";
import L1CTSD from "./components/page-lung-1l-combination-therapy-study-design";
import Registration from "./components/page-registration";
import EditProfile from './components/page-editprofile';
import Forgotpassword from './components/page-forgotpassword';
import Changepassword from './components/page-changepassword';
import LegalacceptancesForm from './components/page-legalacceptances';
import Login from './components/page-signin';
import { pwaPopup } from "./components/partials/PWA";

import { addLoading, removeLoading } from "./utils/loading.js";

import { redirectAuth, signout } from './utils/detectAuth';
import detectPage from './utils/detectPage';

export default () => {
  redirectAuth();
  addLoading();

  const {
    isMenuRight,
    isHome,
    isPwaPopup,
    isCt1,
    isCt2,
    isCt3,
    isRegistration,
    isEditProfile,
    isLogIn,
    isForgotPassword,
    isChangePassword,
    isLegalacceptances
  } = detectPage();

  setTimeout(() => {
    removeLoading();
    signout();
    if (isLogIn) {
      appendToBefore("#content", "#login");
      Login();
    } else {
      appendToBefore("#content", "#page-wrapper");
      if (isMenuRight) {
        MenuRight();
      }
      if (isHome) {
        Home();
      }
      if (isCt1) {
        Lung();
      }
      if (isCt2) {
        L1CT();
      }
      if (isCt3) {
        L1CTSD();
      }
      if (isPwaPopup) {
        pwaPopup();
      }
      if (isRegistration) {
        Registration();
      }
      if (isEditProfile) {
        EditProfile();
      }
      if (isForgotPassword) {
        Forgotpassword();
      }
      if (isChangePassword) {
        Changepassword();
      }
      if (isLegalacceptances) {
        LegalacceptancesForm();
      }
    }

  }, 1500);

};
