import { appendChild } from "../../utils/rearrangeLayout.js";
import HandleEvents from "../../utils/handleEvents.js";
import HandleLists from "../../utils/handleList.js";

export default () => {
  setTimeout(() => {
    appendChild("#content", "#content_level_3");
    const listImg = new HandleLists(".zoom_wrap .zoom_wrap__thumbnail img");
    listImg.zoomImg(".zoom_wrap .zoom_wrap__show");
    const zoomPopup = new HandleEvents(".zoom_wrap .zoom_wrap__show");
    zoomPopup.removeClass(".zoom_wrap .close", "active");
  });
};
