class HandleList {
  constructor(targetStr) {
    this.t = document.querySelectorAll(targetStr);
  }

  addOneItem = (pos, htmlStr) => {
    const newHtml = document.createElement("div");
    newHtml.innerHTML = htmlStr.trim();
    // console.log(`newHtml.firstChild: `, newHtml.firstChild);

    pos.appendChild(newHtml.firstChild);
  };

  remove = target => {
    const oldImg = document.querySelector(target);
    if (oldImg) {
      oldImg.parentNode.removeChild(oldImg);
    }
  };

  zoomImg = popupStr => {
    const popup = document.querySelector(popupStr);
    Array.from(this.t).forEach(item => {
      const newImg = new Image();
      newImg.onload = () => {
        const height = newImg.height;
        const width = newImg.width;
      };
      const strScr = item.getAttribute("src");
      newImg.src = strScr;

      item.addEventListener("click", () => {
        this.remove(".zoom_wrap .zoom_wrap__show img");
        const rawImg = `<img src=${newImg.src} alt=""/>`;
        this.addOneItem(popup, rawImg);
        popup.classList.add("active");
      });
    });
  };

  reArrangeMenu = (target1, target2) => {
    Array.from(this.t).forEach(item => {
      const t1 = item.querySelector(target1);
      const t2 = item.querySelector(target2);
      
      const fetchJSON = JSON.parse(
        item.querySelector(target1).getAttribute("src")
      );
      t1.setAttribute("src", fetchJSON.src);
      t2.setAttribute("href", fetchJSON.href);
    });
  };

  addClass = strName => {
    Array.from(this.t).forEach(item => {
      item.addEventListener("click", e => {
        e.preventDefault();
        Array.from(this.t).forEach(clearActiveAll => {
          clearActiveAll.classList.remove(strName);
        });
        item.classList.add(strName);
        item.querySelector("button.open").classList.add("active");
      });
    });
  };

  addFreeClass = strName => {
    Array.from(this.t).forEach(item => {
      item.addEventListener("click", e => {
        e.preventDefault();
        if (item.className.indexOf("active") > -1) {
          item.classList.remove(strName);
        } else {
          item.classList.add(strName);
        }
      });
    });
  };

  addMoreItem = htmlStr => {
    Array.from(this.t).map(pl => {
      // pl: parentLi
      Array.from(pl.getElementsByTagName("ul")).map(cul => {
        // cul :childUl
        const listLi = Array.from(cul.getElementsByTagName("li"));
        if (listLi.length > 0) {
          const newHtml = document.createElement("div");
          newHtml.innerHTML = htmlStr.trim();
          console.log(`newHtml.firstChild: `, newHtml.firstChild);

          pl.appendChild(newHtml.firstChild);
        }
      });
    });
  };
}
export default HandleList;
