import { appendChild, addElement, addAttr, replaceText } from "../../utils/rearrangeLayout";
import selectBox from "../../utils/selectbox";

export default () => {
  setTimeout(() => {
    resetLayout();
    if(resetLayout()) {
      addCheckmark();
    } else {
      console.log('checkmark missing');
    }

    addElement(
      '<h2>log in information</h2>',
      "#editProfile > .capture_header h3"
    );
    addElement(
      '<h2>PERSONAL INFORMATION</h2>',
      '#capture_editProfile_form_item_newPassword'
    );
    addElement(
      '<h2>PROFESSIONAL INFORMATION</h2>',
      '#capture_editProfile_form_item_phone'
    );
    selectBox();
  });
  const resetLayout = () => {
    appendChild("#content", "#editprofilepage");
    return true;
  }
  const addCheckmark = () => {
    addElement(
      '<span class="checkmark"></span>',
      '#capture_editProfile_form_item_inner_brandedConsent label input'
    );
  }
}