export const appendChild = (parent, child) => {
  const p = document.querySelector(parent);
  const c = document.querySelector(child);
  p.appendChild(c);
}

export const appendToBefore = (target, destination) => {
  const t = document.querySelector(target);
  const d = document.querySelector(destination);
  d.parentNode.insertBefore(t, d.previousSibling);
};
export const appendTo = (target, destination) => {
  const t = document.querySelector(target);
  const d = document.querySelector(destination);
  d.parentNode.insertBefore(t, d.nextSibling);
};

export const addAttr = (target, attr, val) => {
  const t = document.querySelector(target);
  console.log(t);
  t.setAttribute(attr, val);
};

export const replaceText = (target, val) => {
  const t = document.querySelector(target);
  t.innerHTML = val;
};

export const addElement = (el, destination) => {
  const d = document.querySelector(destination);
  console.log(d);
  
  d.insertAdjacentHTML('afterend', el);
}