import { appendChild } from "../../utils/rearrangeLayout";
import CarouselFlex from "../../utils/carouselFlex.js";
import HandleEvents from "../../utils/handleEvents.js";

export default () => {
  setTimeout(() => {
    appendChild("#content", "#content_level_2");

    const routerTo = new HandleEvents(
      "#content_level_2 .main__header .header__left .back",
    );
    routerTo.routerToPage("https://oncology-msdconnect-sg-dev.epublishmerck.com/lung/index.xhtml");

    const cf = new CarouselFlex(
      ".carousel_site_map ul",
      ".carousel_site_map .btn"
    );
    cf.moveLeft();
  });
};