import { appendChild } from "../../../utils/rearrangeLayout";
import HandleEvents from "../../../utils/handleEvents";
import HandleLists from "../../../utils/handleList";
import HandleData from "../../../utils/handleData";

export default () => {
  setTimeout(() => {
    appendChild("#content", ".menu_mobile.menu");
    appendChild(".menu__body .dropdown", "#QC_Menu_Nodes");

    const handleList = new HandleLists(".dropdown > ul > li");
    handleList.addMoreItem(`<div class="item_wrap"><button class="narrow btn">
                            <img src="https://oncology-msdconnect-sg-dev.epublishmerck.com/static/mcisg/images/minus.svg" alt="" />
                          </button>
                          <button class="expand btn">
                            <img src="https://oncology-msdconnect-sg-dev.epublishmerck.com/static/mcisg/images/plus.svg" alt="" />
                          </button></div>`);
    const handleMenu = new HandleEvents(".menu_mobile");
    handleMenu.addClass(".header__right .hamburger", "active");
    handleMenu.removeClass(".menu_header_right .close", "active");

    const replaceMenuElements = {
      search: {
        origin_target: ".menu_header_right .search",
        origin_dom: [
          ".menu__header .menu_header_left",
          ".menu__header .menu_header_right"
        ],
        back_origin_target: ".menu__header .input_search__menu .clearSeach",
        replace_dom: [".menu__header .input_search__menu"],
        position: ".menu__header"
      }
    };
    handleMenu.replaceHtml(
      replaceMenuElements.search.origin_target,
      replaceMenuElements.search.origin_dom,
      replaceMenuElements.search.replace_dom
    );
    handleMenu.replaceHtml(
      replaceMenuElements.search.back_origin_target,
      replaceMenuElements.search.replace_dom,
      replaceMenuElements.search.origin_dom
    );

    const handleSearchMenu = {
      targetEvent: ".header__right .search",
      actions: [
        {
          name: "add",
          html: ".menu_mobile",
          class: "active"
        },
        {
          name: "add",
          html: ".menu_mobile .menu__header .menu_header_left",
          class: "hiddenElement"
        },
        {
          name: "add",
          html: ".menu_mobile .menu__header .menu_header_right",
          class: "hiddenElement"
        },
        {
          name: "remove",
          html: ".menu_mobile .menu__header .input_search__menu",
          class: "hiddenElement"
        }
      ]
    };

    handleMenu.handleAction(
      handleSearchMenu.targetEvent,
      handleSearchMenu.actions
    );

    const handleData = new HandleData(
      ".post .post__body .body__left .body_left_name"
    );
    handleData.localStorageData("uname");
  });
};

