import { isProd } from "../../env";
import {
  appendToBefore,
  appendChild,
  addAttr,
  replaceText,
  addElement
} from "../../utils/rearrangeLayout";

export default () => {
  setTimeout(() => {
    resetLayout();
    if (resetLayout()) {
      addLogo();
      appendToBefore(".rememberme_box", ".register_button");
      appendToBefore(".forgotPassword_box", ".register_button");

      addAttr(".capture_form_item input.capture_currentPassword", "placeholder", "*********");
      addAttr(
        "#capture_signIn_signInEmailAddress",
        "placeholder",
        "name@email.com"
      );
      replaceText(".login_button button", "SIGN IN");
      replaceText(".forgotPassword_box a", "Forgot Password?");
      replaceText(".register_button a", "Register Now");

      // detectAuth
      if (!isProd) {
        const loginBtn = document.querySelector('.login_button .capture_secondary');
        const dataLocalStorage = {
          uuid: "85dfef1c-f96f-4ac5-9071-de455ea1f6a6",
          email: "haritha.saidurjay+sgdev@gmail.com",
          displayName: null,
          mainSpecialty: "DR",
          registrationCountry: "SG",
          validationStatus: "validated",
        };
        loginBtn.addEventListener('click', () => {
          const parseData = JSON.stringify(dataLocalStorage);
          localStorage.setItem("janrainCaptureProfileData", parseData)
          window.location.replace('/index.html');
        });
      }

    } else {
      console.log('resetLayout was fail!');
    }
  });

  const resetLayout = () => {
    appendChild("#content", "#login");
    appendChild("#content", "#bottomLoginBox");
    return true;
  }
  const addLogo = () => {
    addElement(
      '<a class="img_wrap" href="/"><img src="https://oncology-msdconnect-sg-dev.epublishmerck.com/static/mcisg/images/MSD_Logo_white.cfd55b11678740dabc17.svg" alt="" /></a>',
      "#signIn > .capture_header h1"
    );
  }
  const loginBtn = document.querySelector('.login_button button');
  loginBtn.addEventListener('click', () => {
    appendToBefore("#capture_signIn_signInForm_errorMessages", ".login_button");
  });
}
