export const addLoading = () => {
  const loadingHtmlStr =
    `<div 
      class="loading_page" 
      style="position: fixed;
        height: 100vh;
        width: 100vw;
        top: 0;
        left: 0;
        background-color: #f9f9f9;
        z-index: 5;"
    >
    <img 
      src="https://oncology-msdconnect-sg-dev.epublishmerck.com/static/mcisg/images/spiner.gif"
      style="
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 90%;"
      alt=""/>
    </div>`;
  const loadingPage = document.createElement("div");
  loadingPage.innerHTML = loadingHtmlStr.trim();
  const body = document.querySelector("body");
  body.appendChild(loadingPage.firstChild);
};

export const removeLoading = () => {
  const lp = document.querySelector(".loading_page");
  if (lp) {
    lp.parentNode.removeChild(lp);
  }
};
