import { isAndroid, iOS, isWindow, isMacLike } from "../../../utils/detectDevice";
import { appendChild } from "../../../utils/rearrangeLayout";

export const pwaPopup = () => {
  setTimeout(() => {
    
    appendChild("#content", ".pwa.ios");
    appendChild("#content", ".pwa.android");
    const pwaBtn = document.querySelector(".add_to_home_screen_button");
    
    
    const popupIos = document.querySelector("#content .pwa.ios");
    const closePopupIos = document.querySelector(
      "#content .pwa.ios .main__header .close"
    );

    const popupAndroid = document.querySelector("#content .pwa.android");
    const closePopupAndroid = document.querySelector(
      "#content .pwa.android .main__header .close"
    );

    if (isAndroid || isWindow) {
      console.log(`isAndroid: `, isAndroid, `DETAIL`, navigator.userAgent);

      pwaBtn.addEventListener("click", () => {
        popupAndroid.classList.add("active");
      });
      closePopupAndroid.addEventListener("click", () => {
        popupAndroid.classList.remove("active");
      });
    } else if (iOS) {
      pwaBtn.addEventListener("click", () => {
        popupIos.classList.add("active");
      });
      closePopupIos.addEventListener("click", () => {
        popupIos.classList.remove("active");
      });
    } else {
      // if (isWindow) {
      //   console.log(`windows`, isWindow);
      // }
      if (isMacLike) {
        console.log(`Mac`, isMacLike);
      }
    }
  }, 1000);
};
