class HandleData {
    constructor(strTarget) {
        this.t = document.querySelector(strTarget);
    }

    localStorageData = (lsKey) => {
        const itemLs = localStorage.getItem(lsKey);
        
        if(itemLs) {
            const data = document.createTextNode(itemLs)
            this.t.appendChild(data);
        } else {
            console.log('Getting data localStorage is not available!');
        }
    }

    apiData = () => {

    }

}

export default HandleData;